import request from "@/network/request"

export function addRegUser(data) {
    return request({
        url: '/api/addRegUser',
        method: 'post',
        data,
        headers: {'Content-Type': 'application/json;charset=UTF-8'}
    })
}

export function getHeaderDetail() {
    return request({
        url: '/api/getHeaderDetail',
        method: 'get',
        params: {},
    })
}

