import axios from 'axios'
import store from '@/store'
import qs from "qs";
// import { getToken } from '@/utils/auth'

//process.env.NODE_ENV

const tokenName = 'auth-token';

// create an axios instance
const axiosInstance = axios.create({
    baseURL: process.env.VUE_APP_BASE_API, // url = base url + request url
    withCredentials: true, // send cookies when cross-domain requests
    timeout: 100 * 1000, // request timeout
    // headers: {'Content-Type': 'application/x-www-form-urlencoded'}
})

// request interceptor
axiosInstance.interceptors.request.use(
    config => {
        // 
        config.params = {
            _t: new Date().getTime(),
            ...config.params
        }
        if (config.method === 'post') {
            Object.keys(config.data).forEach(item => {
                if (config.data[item] === null) {
                    delete config.data[item]
                }
            })
            // application/json  Content-Type
            if (config.headers['Content-Type'].includes('x-www-form-urlencoded')) {
                config.data = qs.stringify(config.data)
            }
            if (!config.headers['Content-Type']) {//post Content-Type default to application
                config.headers.set("Content-Type", 'application/json;charset=UTF-8');
            }
        }

        let token = sessionStorage.getItem(tokenName)
        if (!token) {
            // token = getToken() || ''
        }
        config.headers[tokenName] = token;
        return config
    },
    error => {
        // do something with request error
        console.log(error) // for debug
        return Promise.reject(error)
    }
)

let errorCodeMap = new Map();
errorCodeMap.set(302, "");
errorCodeMap.set(500, "");
// response interceptor
axiosInstance.interceptors.response.use(
    response => {
        const res = response.data;
        let errorText = errorCodeMap.get(res.code);
        if (errorText) {
            alert(errorText ?? "");
            return Promise.reject(res);
        }
        return res;
    },
    error => {
        const {status, message} = error?.response?.data || {};
        return Promise.reject(error)
    }
)

export default axiosInstance
